/* @import "compass/css3"; */

.set p span {
    color: 'red';
    position: relative;
    -webkit-animation: fly 10s linear alternate;
}

@-webkit-keyframes fly {
    0% {
        opacity: 0;
        top: -30px;
        left: -30px;
    }
    50% {
        opacity: 0.5;
    }
    100% {
        opacity: 1;
        top: 0;
        left: 0;
    }
}